import React from 'react';
import {Group, HeaderButton, IOS, List, Panel, PanelHeader, platform} from "@vkontakte/vkui";
import Cell from "@vkontakte/vkui/dist/components/Cell/Cell";
import Icon28ChevronBack from '@vkontakte/icons/dist/28/chevron_back';
import Icon24Back from '@vkontakte/icons/dist/24/back';
import prepare from "../../handlers/prepare";

const Receipt = ({items, id, dateTime, go}) => {
	const osName = platform();
	const receiptItem = (items.map((item, index) => {
		const sum = item.income ? item.sum / 100 : (-1) * item.sum / 100;
		const stl = sum > 0 ? {color: 'green'} : {color: 'red'};
		return(
			<Group title={item.name} key={index}>
				<List>
					{item.price ? <Cell indicator={new Intl.NumberFormat('ru-RU', {style: 'currency', currency: 'RUB'}).format(item.price / 100)}>Цена</Cell> : null}
					{item.quantity ? <Cell indicator={item.quantity}>Кол-во</Cell> : null}
					<Cell
						asideContent={new Intl.NumberFormat('ru-RU', {style: 'currency', currency: 'RUB'}).format(sum)}
						style={stl}
					>
						Сумма
					</Cell>
				</List>
			</Group>
		)
	}));

	return(
		<Panel id={id}>
			<PanelHeader left={<HeaderButton onClick={go} data-to="home">
				{osName === IOS ? <Icon28ChevronBack/> : <Icon24Back/>}
			</HeaderButton>}>
				{prepare.date(dateTime)}
			</PanelHeader>
			{receiptItem}
		</Panel>
	)
};

export default Receipt;